class Forums extends React.Component {
  forums;
  messagesEndRef = React.createRef();
  constructor(props) {
	 // console.log("forums construct");
    super(props);
    var tmp = this.props.globalMessage;
    this.state = {
      globalMessage: tmp
    };
  }
  setelem(item){ 
	  this.setState(function (state) { 
		  var tmp = this.props.globalMessage;
		  return {globalMessage: tmp}}
		  );
    }
  cleanup(){
	  this.setState({globalMessage:[]});  
	}
  
  componentDidMount () {
    this.scrollToBottom()
  }
  componentDidUpdate () {
    this.scrollToBottom()
  }
  scrollToBottom = () => {
    this.messagesEndRef.current.scrollIntoView({ behavior: 'smooth' })
  }
  render() {
	//console.log("forums rendered");
    return (this.forums = (
      <div id="column-middle">
        {this.state.globalMessage.map((messageItem) => {
          if (messageItem.type === "message")
            return (
              <div className="messagebox">
                {date_format(messageItem.time)} {messageItem.username} {messageItem.message}
              </div>
            );
          else if (messageItem.type === "server")
            return (
              <div className="messagebox">
                {date_format(messageItem.time)} {messageItem.message}
              </div>
            );
          else if (messageItem.type === "part")
            return (
              <div className="messagebox">
                {date_format(messageItem.time)} {messageItem.username} has part
              </div>
            );
            else if (messageItem.type === "join")
            return (
              <div className="messagebox">
                {date_format(messageItem.time)} {messageItem.username} has join
              </div>
            );
          else return <div />;
        })}
      <div ref={this.messagesEndRef} />
      </div>
    ));
  }
}

class MessageBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      message: null,
      disconnect: false
    };
    this.sendingHandler = this.sendingHandler.bind(this);
    this.swit = this.swit.bind(this);
  }

  /*componentWillReceiveProps(nextProps) {
    this.setState({ disconnect: this.props.disconnect });
  }*/
  swit(prop){
	  console.log("ppp")
	  this.setState({'disconnect':prop});
	  console.log(this.state.disconnect)
	  }
  sendingHandler(event) {
    this.setState({ message: event.target.value });
    var messageToken = sessionStorage.getItem("messageToken");
    if (messageToken === null || event.nativeEvent.keyCode !== 13) {
      console.log("token invalid or keycode");
      return;
    }

    event.preventDefault();
    if (this.state.message === "") {
      return;
    }

    var form = new FormData();
    form.append("message", this.state.message);
    var request = new XMLHttpRequest();
    request.open("POST", sessionStorage.getItem("url") + "/message");
    request.setRequestHeader("Authorization", "Bearer " + messageToken);
    request.onreadystatechange = function (event) {
      if (
        event.target.readyState === 4 &&
        event.target.status !== 403 &&
        messageToken != null
      ) {
        messageToken = event.target.getResponseHeader("token");
        sessionStorage.setItem("messageToken", messageToken);
      }
    };
    request.send(form);
    sessionStorage.setItem("messageToken", null);
    event.target.value = "";
    this.setState({ message: event.target.value });
  }
  render() {
    return (
      <input
        id="message"
        onChange={this.sendingHandler}
        onKeyPress={this.sendingHandler}
        type="text"
        defaultValue={this.state.disconnect ? "disconnect" : ""}
        disabled={this.state.disconnect}
      />
    );
  }
}
class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = { timer: new Date() };
  }
  tick() {
    this.setState({ timer: new Date() });
  }
  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 1000);
  }
  render() {
    return <div id="time">{this.state.timer.toLocaleTimeString()}  online users</div>;
  }
}
class UsersList extends React.Component {
  constructor(props) {
	//console.log("users construct");
    super(props);
    var tmp = globalusers;
    //console.log(globalusers)
    this.state = {
      usersList: tmp
	};
  }

   componentDidUpdate(prevProps) {
	if(prevProps.usersList !== this.props.usersList) {
		this.setState({usersList: this.props.usersList});
	}
  }

  render() {
	  //console.log("users rendered");
	  //console.log(this.state.usersList)
		//console.log(globalusers)
    return (
      <ul id="user_list">
        {this.state.usersList.map((users) => (
          <li> {users} </li>
        ))}
      </ul>
    );
  }
}

class MainApp extends React.Component {
  stream;
  messageBarEle = React.createRef();
  cleaner = React.createRef();
  constructor(props) {
    super(props);
    this.state = {
      globalMessage: [],
      usersList: [],
      message: "",
      disconnect: true
    };
    //this.start_stream = this.start_stream.bind(this);
    this.joinHandler = this.joinHandler.bind(this);
    this.messageHandler = this.messageHandler.bind(this);
    this.partHandler = this.partHandler.bind(this);
    this.usersHandler = this.usersHandler.bind(this);
    this.disconnectHandler = this.disconnectHandler.bind(this);
    this.serverStatusHandler = this.serverStatusHandler.bind(this);
    this.errorHandler = this.errorHandler.bind(this);
    this.openHandler = this.openHandler.bind(this);
    this.lastHandler = this.lastHandler.bind(this);
  }
  /*tick() {
    this.setState({ timer: new Date() });
  }
  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 1000);
  }*/
  errorHandler(event) {
	  console.log("error")
	  this.messageBarEle.current.swit(true)
	  this.setState({usersList:[],disconnect:true})
      console.log("Disconnected, retrying");
    //}
  }
  messageHandler(event) {
    var data = JSON.parse(event.data);
    var tmpHash = {
      time: data["created"],
      username: data.user,
      message: data.message,
      type: "message"
    };
    var tmp = this.state.globalMessage;
    tmp.push(tmpHash);
    this.setState({ globalMessage: tmp });
    this.cleaner.current.setelem(tmpHash);
  }

  disconnectHandler(event) {
    this.stream.close();
    this.setState({disconnect:true})
    sessionStorage.setItem("messageToken", null);
    sessionStorage.setItem("streamToken", null);
    render(<LoginPage />, document.getElementById("root"));
    console.log("disconnect")
  }
  
  partHandler(event) {
    var data = JSON.parse(event.data);
    var tmpHash = {
      time: data["created"],
      username: data.user,
      message: null,
      type: "part"
    };
    var tmp = this.state.globalMessage;
    tmp.push(tmpHash);
    this.setState({ globalMessage: tmp });
    console.log(this.state.globalMessage)
    var tmp2 = removeItemOnce(this.state.usersList,data.user);
    this.setState( {usersList: tmp2});
    console.log(this.state.usersList)
    console.log("part")
  }
  openHandler(event){
	  this.setState({disconnect: false});
	  this.messageBarEle.current.swit(false)
	  console.log("open")
	  }
  usersHandler(event) {
	console.log("users")
    var data = JSON.parse(event.data).users;
    //console.log(data)
    this.setState({ usersList: data});
    globalusers = data;
    console.log(this.state.usersList)
  }
  
  
  serverStatusHandler(event) {
    var data = JSON.parse(event.data);
    var tmpHash = {
      time: data["created"],
      username: null,
      message: data.status,
      type: "server"
    };
    var tmp = this.state.globalMessage;
    tmp.push(tmpHash);
    this.setState({ globalMessage: tmp });
    console.log("server")
  }
  
	lastHandler(event) {
    console.log('Received a message event: [' + event.lastEventId + ']', event.data);
	}
  
  joinHandler(event) {
    var data = JSON.parse(event.data);
    var tmpHash = {
      time: data["created"],
      username: data.user,
      message: null,
      type: "join"
    };
    var tmp = this.state.globalMessage;
    tmp.push(tmpHash);
    this.setState({ globalMessage: tmp });
    console.log(data.user)
    if (!this.state.usersList.includes(data.user)){
		this.state.usersList.push(data.user);
		this.setState(function(state) {
		  return {
			usersList: state.usersList
		  };
		});
	}
	console.log(this.state.usersList)
    console.log("join")
  }
  componentDidMount() {
    this.stream = new EventSource(
      sessionStorage.getItem("url") +
        "/stream/" +
        sessionStorage.getItem("streamToken")
    );
    this.stream.addEventListener("error", this.errorHandler, false);
    this.stream.addEventListener("Disconnect", this.disconnectHandler, false);
    this.stream.addEventListener("Message", this.messageHandler, false);
    this.stream.addEventListener("Part", this.partHandler, false);
    this.stream.addEventListener(
      "ServerStatus",
      this.serverStatusHandler,
      false
    );
    this.stream.addEventListener('Message', this.lastHandler, false);
    this.stream.addEventListener("open", this.openHandler);
    this.stream.addEventListener("Users", this.usersHandler, false);
    this.stream.addEventListener("Join", this.joinHandler, false);
    var elem = document.getElementById('column-middle');
	elem.scrollTop = elem.scrollHeight;
  }
  render() {
    //console.log("Main rendered");
    //console.log(this.state.usersList)
    //console.log(globalusers)
    return (
      <div className="App">
        <header className="App-header">
          <div id="welcome-notice">
            welcome {this.state.disconnect?"reconnecting":sessionStorage.getItem("username")}
          </div>
        </header>
        <div className="App-body">
          <div className="sub-title">
            <div id="webpage-name"> suebchat </div>
            <div id="subtitle-main">Forums </div>
            <Clock />
          </div>
        </div>
        <div className="App-main">
          <div id="column-left">
            <li id="stream">streams</li>
          </div>
          <Forums globalMessage={this.state.globalMessage} ref={this.cleaner}/>
          <div id="column-right">
            <ul id="user_list"> 
				{this.state.usersList.map((users) => (
					<li> {users} </li>
				))}
			</ul>
          </div>
        </div>
        <footer>
          <MessageBar disconnect={this.state.disconnect} ref={this.messageBarEle}/>
        </footer>
      </div>
    );
  }
}

class LoginPage extends React.Component {
  username;
  url;
  password;
  urlValue;
  constructor(props) {
    super(props);
    this.state = {
      userVal: "",
      urlVal: "http://localhost:3001",
      passVal: ""
    };
    this.urlHandler = this.urlHandler.bind(this);
    this.userHandler = this.userHandler.bind(this);
    this.passwordHandler = this.passwordHandler.bind(this);
    this.handleEnter = this.handleEnter.bind(this);
    this.username = (
      <input id="username" onChange={this.userHandler} type="text" />
    );
    this.url = (
      <input
        id="url"
        defaultValue={"http://localhost:3001"}
        onChange={this.urlHandler}
        type="text"
      ></input>
    );
    this.password = (
      <input id="password" onChange={this.passwordHandler} type="password" />
    );
  }
  /*componentDidMount() {
    this.urlHandler = this.urlHandler.bind(this);
    this.userHandler = this.userHandler.bind(this);
    this.passwordHandler = this.passwordHandler.bind(this);
  }*/
  userHandler(event) {
    this.setState({ userVal: event.target.value });
  }
  urlHandler(event) {
    this.setState({ urlVal: event.target.value });
  }
  passwordHandler(event) {
    this.setState({ passVal: event.target.value });
  }
  handleEnter(event) {
    if (event.nativeEvent.keyCode === 13) {
      console.log("running");
      if (!this.state.passVal || !this.state.userVal || !this.state.urlVal) {
        alert("empty fields!");
        console.log(this.state.passVal);
        console.log(this.state.userVal);
        console.log(this.state.urlVal);
        return;
      } else {
        var form = new FormData();
        form.append("password", this.state.passVal);
        form.append("username", this.state.userVal);
        sessionStorage.setItem("url", this.state.urlVal);
        const request = new XMLHttpRequest(),
          method = "POST",
          url = sessionStorage.getItem("url");
        request.open(method, url + "/login", true);
        //const request = new XMLHttpRequest(),"GET",sessionStorage.getItem("url");
        request.onreadystatechange = function () {
          if (request.readyState !== 4) {
            return;
          }
          if (request.status === 201) {
            const data = JSON.parse(this.responseText);
            sessionStorage.setItem("messageToken", data.message_token);
            sessionStorage.setItem("streamToken", data.stream_token);
            render(<MainApp />, document.getElementById("root"));
            return;
          } else if (request.status === 403) {
            alert("Invalid username or password");
            return;
          } else if (request.status === 200) {
            return;
          } else {
            alert(request.status + " failure to /login");
            return;
          }
        };
        sessionStorage.setItem("username", this.state.userVal);
        request.send(form);
      }
    } else {
      return;
    }
  }
  render() {
    return (
      <div id="login-modal">
        <div className="content" onKeyPress={this.handleEnter}>
          <h2>Login</h2>
          <div>
            <label>
              Chat URL <br />
              {this.url}
            </label>
          </div>
          <div>
            <label>
              Username <br />
              {this.username}
            </label>
          </div>
          <div>
            <label>
              Password <br />
              {this.password}
            </label>
          </div>
        </div>
      </div>
    );
  }
}

export default function App() {
  if (sessionStorage.getItem("messageToken") === null) {
    //alert("login-show");
    return <LoginPage />;
  } else {
    //console.log(sessionStorage.getItem("messageToken"));
    return <MainApp />;
  }
}

window.onunload = function () {
  sessionStorage.removeItem("messageToken");
  sessionStorage.removeItem("streamToken");
};
